.wp-block-group.alignfull .wp-block-column > .wp-block-image img {
  height: 100%;
  width: 100%;
}
.wp-block-group.alignfull .wp-block-columns {
  margin-bottom: 0;
}
.wp-block-group.bloc-bleu {
  background-color: var(--bleu);
  padding: 3rem;
}
.wp-block-group.bloc-bleu p, .wp-block-group.bloc-bleu h3, .wp-block-group.bloc-bleu h4 {
  color: var(--wp--preset--color--blanc);
}
@media screen and (max-width: 781px) {
  .wp-block-group.bloc-bleu {
    width: 100vw;
    margin-left: 50%;
    transform: translateX(-50%);
    margin-bottom: -1.8em;
  }
}
@media screen and (max-width: 781px) and (min-width: 1920px) {
  .wp-block-group.bloc-bleu {
    width: var(--largeur-max);
  }
}

.wp-block-columns.alignfull {
  margin-bottom: 0;
  padding: var(--margin-y-2) var(--padding-x);
}
.wp-block-columns a.wp-block-button__link {
  min-width: 10rem;
}

.wp-block-column.gap-fallback {
  padding-left: 2em;
}
@supports (font-size: clamp(1em, 10vw, 10em)) {
  .wp-block-column.gap-fallback {
    padding-left: 0;
  }
}

.wp-block-image.alignwide {
  width: 100vw;
  margin-left: 50%;
  transform: translateX(-50%);
  padding: 0 var(--padding-x);
}
@media (min-width: 1920px) {
  .wp-block-image.alignwide {
    width: var(--largeur-max);
  }
}
.wp-block-image.alignwide figcaption {
  text-align: right;
}

.wp-block-code {
  background-color: lightgrey;
  margin-bottom: var(--margin-y-2);
  padding: 2rem;
  font-style: italic;
}

.filtre-archive {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  padding-top: 2rem;
}
.filtre-archive label {
  color: var(--wp--preset--color--primaire);
  cursor: pointer;
  font-size: calc(20 * var(--taille-texte) / 15);
  font-weight: 400;
  margin: 0 1.5rem 1rem 0;
  padding: 0;
  position: relative;
  transition: all 0.4s linear;
}
.filtre-archive label:hover, .filtre-archive label:focus {
  text-decoration: underline;
}
.filtre-archive input {
  display: block;
  appearance: auto;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
  /* Many screen reader and browser combinations announce broken words as they would appear visually. */
}
.filtre-archive input:focus-visible + label {
  text-decoration: underline;
}
.filtre-archive input:checked + label {
  font-weight: 700;
}
@media (min-width: 960px) {
  .filtre-archive {
    justify-content: center;
    margin-bottom: 2rem;
  }
  .filtre-archive label {
    padding: 0.5rem 2.5rem;
  }
}

.pagination {
  align-items: center;
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 0 var(--padding-x);
  margin: var(--margin-y-1) 0;
}
.pagination li::after {
  color: var(--wp--preset--color--primaire);
  content: "-";
  display: inline-block;
  font-size: calc(20 * var(--taille-texte) / 15);
  font-weight: 700;
}
.pagination li:last-of-type::after {
  display: none;
}
.pagination a.page {
  font-size: calc(20 * var(--taille-texte) / 15);
  color: var(--wp--preset--color--primaire);
  font-weight: 700;
  margin: 0 0.5rem;
  padding: 0.5rem;
  text-decoration: none;
}
.pagination a.page:hover, .pagination a.page:focus {
  text-decoration: underline;
}
.pagination li.active a {
  text-decoration: underline;
}
.pagination li.disabled a {
  cursor: default;
}
.pagination li.disabled a:hover, .pagination li.disabled a:focus {
  border-color: transparent;
  color: var(--bleu);
}

.post-summary {
  display: flex;
  flex-direction: column;
  margin: 0 auto var(--margin-y-1);
  max-width: 46rem;
  position: relative;
}
.post-summary p.entry-date {
  color: var(--wp--preset--color--primaire);
}
.post-summary a.suite {
  align-items: center;
  border-radius: 50%;
  display: flex;
  height: 2.6rem;
  justify-content: center;
  margin-bottom: 0;
  padding: 0;
  padding-left: 2px;
  width: 2.6rem;
}
.post-summary a.suite svg {
  max-height: 60%;
  max-width: 90%;
}

.post-summary__title {
  line-height: 1.2;
  margin-bottom: 0;
}
.post-summary__title a {
  text-decoration: none;
}

.post-summary__image {
  display: block;
  margin-bottom: 0.5rem;
}
.post-summary__image img {
  aspect-ratio: 1.36;
  display: block;
  width: 100%;
}
.post-summary__image.contain {
  border-bottom: solid 1px lightgrey;
  padding: 1rem;
}
.post-summary__image.contain img {
  object-fit: contain;
}
@supports not (aspect-ratio: auto) {
  .post-summary__image {
    height: 257px;
  }
  .post-summary__image img {
    height: 100%;
  }
}

@media (min-width: 768px) {
  .post-summary {
    flex-direction: column;
  }
  .post-summary__image {
    flex: unset;
    margin-right: unset;
  }
  .post-summary__content {
    flex-grow: 1;
  }
}
.nav-social {
  width: 100%;
  --size: 3.2rem;
}
@media (min-width: 960px) {
  .nav-social {
    --size:4rem;
  }
}
.nav-social ul {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  width: 100%;
}
.nav-social ul li a {
  background-color: var(--wp--preset--color--citron-main);
  background-image: url("icons/facebook.svg");
  border-radius: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 25%;
  color: transparent;
  display: block;
  flex: 0 0 var(--size);
  height: var(--size);
  margin-right: 30px;
  overflow: hidden;
  width: var(--size);
}
.nav-social ul li a[href*=insta] {
  background-image: url("icons/insta.svg");
  background-size: 34.375%;
}
.nav-social ul li a[href*=soundcloud] {
  background-image: url("icons/soundcloud.svg");
  background-size: 37.5%;
}
.nav-social ul li:last-of-type a {
  margin-right: 0;
}